html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

body {
  background-color: #674baf;
  background-image: url(/images/bg-mobile.svg);
  background-repeat: no-repeat;
  background-size: 100%;
  margin: 0 2.3125rem;
}
@media (min-width: 40em) {
  body {
    background-image: url(/images/bg-desktop.svg);
    background-size: auto 100%;
    margin: 0 4.625rem;
  }
}

.text {
  color: white;
  text-align: center;
}
@media (min-width: 40em) {
  .text {
    text-align: left;
  }
}
.text h1 {
  font-family: "Poppins", sans-serif;
  font-size: 1.625rem;
  line-height: 2.25rem;
  font-weight: 600;
}
@media (min-width: 40em) {
  .text h1 {
    font-size: 2.625rem;
    line-height: 3.75rem;
  }
}
.text p {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 400;
}
@media (min-width: 40em) {
  .text p {
    font-size: 1.125rem;
    line-height: 1.6875rem;
  }
}
.text a, .text a:active, .text a:visited, .text a:link {
  color: #674baf;
  font-family: "Poppins", sans-serif;
  font-size: 0.8125rem;
  font-weight: 400;
  text-decoration: none;
}
@media (min-width: 40em) {
  .text a, .text a:active, .text a:visited, .text a:link {
    font-size: 1.0625rem;
  }
}

header {
  padding-top: 2.1875rem;
  margin-bottom: 3.75rem;
  justify-content: left;
}
@media (min-width: 40em) {
  header {
    padding-top: 3.75rem;
    margin-bottom: 6.25rem;
  }
}
header img {
  width: 7.625rem;
}
@media (min-width: 40em) {
  header img {
    width: 13.125rem;
  }
}

main {
  display: flex;
  flex-direction: column;
  gap: 4.375rem;
}
@media (min-width: 40em) {
  main {
    flex-direction: row;
    gap: 3.75rem;
  }
}
main .image {
  flex: 1.4;
}
main .image img {
  width: 100%;
}
main .text {
  flex: 1;
}
main .text h1 {
  margin-bottom: 1.5625rem;
}
@media (min-width: 40em) {
  main .text h1 {
    margin-top: 3.125rem;
    margin-bottom: 2.1875rem;
  }
}
main .text p {
  margin-bottom: 1.5625rem;
}
@media (min-width: 40em) {
  main .text p {
    margin-bottom: 1.875rem;
  }
}
main .text a {
  background-color: white;
  border-radius: 1.25rem;
  padding: 0.625rem 4.375rem;
  box-shadow: 0 0.625rem 0.625rem rgba(0, 0, 0, 0.25);
}
@media (min-width: 40em) {
  main .text a {
    border-radius: 1.5625rem;
    padding: 0.625rem 4.0625rem;
  }
}
main .text a:hover {
  background-color: #e882e8;
  color: white;
}

footer {
  position: absolute;
  bottom: 2.5rem;
}
footer .logos {
  display: flex;
  gap: 0.5rem;
  color: white;
}
@media (min-width: 40em) {
  footer .logos {
    gap: 1rem;
  }
}
footer .logos__logo {
  width: 1.875rem;
  height: 1.875rem;
  font-size: 1.125rem;
  display: block;
  text-align: center;
  border: 0.0625rem solid;
  border-radius: 50%;
  padding: 0.3125rem;
}
@media (min-width: 40em) {
  footer .logos__logo {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.25rem;
    padding: 0.5625rem;
  }
}
footer .logos__logo:hover {
  color: #e882e8;
}
@media (min-width: 40em) {
  footer {
    right: 5rem;
  }
}
@media (max-width: 39.9375em) {
  footer {
    left: 50%;
    transform: translateX(-50%);
  }
}